  align-items: flex-start;
  margin: 0;
  padding: 10px;
  display: flex;
}

.country-info__title {
  border-radius: 10px;
  align-items: center;
  padding: 5px;
  display: flex;
}

.country-info__title_img {
  width: 50px;
}

.country-info__title_name {
  text-transform: uppercase;
  margin: 0 0 0 10px;
